import { userService, User } from './userService';
import { toast } from 'sonner';

interface BibleBook {
  abbrev: { pt: string; en: string };
  author: string;
  chapters: number;
  group: string;
  name: string;
  testament: string;
}

interface BibleVerse {
  number: number;
  text: string;
}

interface BibleChapter {
  book: {
    abbrev: { pt: string; en: string };
    name: string;
    author: string;
    group: string;
    version: string;
  };
  chapter: {
    number: number;
    verses: number;
  };
  verses: BibleVerse[];
}

interface BibleVersion {
  version: string;
  verses: number;
}

// Mapeamento dos nomes dos livros em portugus para abreviaes
const BOOK_ABBREVIATIONS: Record<string, string> = {
  // Antigo Testamento
  Gnesis: 'gn',
  xodo: 'ex',
  Levtico: 'lv',
  Nmeros: 'nm',
  Deuteronmio: 'dt',
  Josu: 'js',
  Juzes: 'jz',
  Rute: 'rt',
  '1 Samuel': '1sm',
  '2 Samuel': '2sm',
  '1 Reis': '1rs',
  '2 Reis': '2rs',
  '1 Crnicas': '1cr',
  '2 Crnicas': '2cr',
  Esdras: 'ed',
  Neemias: 'ne',
  Ester: 'et',
  J: 'j',
  Salmos: 'sl',
  Provrbios: 'pv',
  Eclesiastes: 'ec',
  Cantares: 'ct',
  Isaas: 'is',
  Jeremias: 'jr',
  Lamentaes: 'lm',
  Ezequiel: 'ez',
  Daniel: 'dn',
  Osias: 'os',
  Joel: 'jl',
  Ams: 'am',
  Obadias: 'ob',
  Jonas: 'jn',
  Miquias: 'mq',
  Naum: 'na',
  Habacuque: 'hc',
  Sofonias: 'sf',
  Ageu: 'ag',
  Zacarias: 'zc',
  Malaquias: 'ml',

  // Novo Testamento
  Mateus: 'mt',
  Marcos: 'mc',
  Lucas: 'lc',
  Joo: 'jo',
  Atos: 'at',
  Romanos: 'rm',
  '1 Corntios': '1co',
  '2 Corntios': '2co',
  Glatas: 'gl',
  Efsios: 'ef',
  Filipenses: 'fp',
  Colossenses: 'cl',
  '1 Tessalonicenses': '1ts',
  '2 Tessalonicenses': '2ts',
  '1 Timteo': '1tm',
  '2 Timteo': '2tm',
  Tito: 'tt',
  Filemom: 'fm',
  Hebreus: 'hb',
  Tiago: 'tg',
  '1 Pedro': '1pe',
  '2 Pedro': '2pe',
  '1 Joo': '1jo',
  '2 Joo': '2jo',
  '3 Joo': '3jo',
  Judas: 'jd',
  Apocalipse: 'ap',
};

const API_BASE_URL = 'https://www.abibliadigital.com.br/api';

class BibliaApiService {
  private user: User | null = null;
  private initializePromise: Promise<void> | null = null;
  private retryCount = 0;
  private maxRetries = 3;

  // Inicializar ou obter usurio existente
  private async initialize(): Promise<void> {
    if (this.user) return;

    // Verificar se existe usurio no localStorage
    const savedUser = localStorage.getItem('biblia_api_user');
    if (savedUser) {
      try {
        this.user = JSON.parse(savedUser);
        // Verificar se o token ainda  vlido fazendo uma requisio teste
        await this.testToken();
        return;
      } catch (error) {
        localStorage.removeItem('biblia_api_user');
        this.user = null;
      }
    }

    // Criar novo usurio com retry
    await this.createUserWithRetry();
  }

  // Criar usurio com tentativas automticas
  private async createUserWithRetry(): Promise<void> {
    for (let i = 0; i < this.maxRetries; i++) {
      try {
        const newUser = await userService.createUser('Bible Reader User');
        if (newUser) {
          this.user = newUser;
          localStorage.setItem('biblia_api_user', JSON.stringify(newUser));
          return;
        }
      } catch (error) {
        if (i < this.maxRetries - 1) {
          // Aguardar um pouco antes da prxima tentativa
          await new Promise((resolve) => setTimeout(resolve, 1000 * (i + 1)));
        }
      }
    }

    // Se todas as tentativas falharam, lanar erro
    throw new Error('No foi possvel criar usurio para a API');
  }

  // Testar se o token  vlido
  private async testToken(): Promise<void> {
    if (!this.user) {
      throw new Error('Usurio no est disponvel');
    }

    const response = await fetch(`${API_BASE_URL}/versions`, {
      headers: {
        Authorization: `Bearer ${this.user.token}`,
        Accept: 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error('Token invlido');
    }
  }

  // Garantir que o servio est inicializado
  private async ensureInitialized(): Promise<void> {
    if (!this.initializePromise) {
      this.initializePromise = this.initialize();
    }
    await this.initializePromise;
  }

  // Fazer requisio autenticada
  private async makeRequest(endpoint: string): Promise<any> {
    await this.ensureInitialized();

    if (!this.user) {
      throw new Error('Usurio no est disponvel');
    }

    const response = await fetch(`${API_BASE_URL}${endpoint}`, {
      headers: {
        Authorization: `Bearer ${this.user.token}`,
        Accept: 'application/json',
      },
    });

    if (!response.ok) {
      // Se o token expirou, tentar renovar
      if (response.status === 401) {
        await this.handleTokenExpired();
        // Tentar novamente aps renovar
        return this.makeRequest(endpoint);
      }
      throw new Error(
        `Erro na API: ${response.status} - ${response.statusText}`
      );
    }

    return response.json();
  }

  // Lidar com token expirado
  private async handleTokenExpired(): Promise<void> {
    this.user = null;
    this.initializePromise = null;
    localStorage.removeItem('biblia_api_user');
    await this.initialize();
  }

  // Obter todos os livros
  async getBooks(): Promise<BibleBook[]> {
    try {
      return await this.makeRequest('/books');
    } catch (error) {
      throw error;
    }
  }

  // Obter detalhes de um livro
  async getBook(bookName: string): Promise<BibleBook> {
    try {
      const abbrev = BOOK_ABBREVIATIONS[bookName];
      if (!abbrev) {
        throw new Error(`Abreviao no encontrada para o livro: ${bookName}`);
      }
      return await this.makeRequest(`/books/${abbrev}`);
    } catch (error) {
      throw error;
    }
  }

  // Obter captulo completo
  async getChapter(
    version: string,
    bookName: string,
    chapter: number
  ): Promise<BibleChapter> {
    try {
      const abbrev = BOOK_ABBREVIATIONS[bookName];
      if (!abbrev) {
        throw new Error(`Abreviao no encontrada para o livro: ${bookName}`);
      }

      return await this.makeRequest(`/verses/${version}/${abbrev}/${chapter}`);
    } catch (error) {
      throw error;
    }
  }

  // Obter versculo especfico
  async getVerse(
    version: string,
    bookName: string,
    chapter: number,
    verse: number
  ): Promise<any> {
    try {
      const abbrev = BOOK_ABBREVIATIONS[bookName];
      if (!abbrev) {
        throw new Error(`Abreviao no encontrada para o livro: ${bookName}`);
      }

      return await this.makeRequest(
        `/verses/${version}/${abbrev}/${chapter}/${verse}`
      );
    } catch (error) {
      throw error;
    }
  }

  // Obter versculo aleatrio
  async getRandomVerse(version: string): Promise<any> {
    try {
      return await this.makeRequest(`/verses/${version}/random`);
    } catch (error) {
      throw error;
    }
  }

  // Obter versculo aleatrio de um livro especfico
  async getRandomVerseFromBook(
    version: string,
    bookName: string
  ): Promise<any> {
    try {
      const abbrev = BOOK_ABBREVIATIONS[bookName];
      if (!abbrev) {
        throw new Error(`Abreviao no encontrada para o livro: ${bookName}`);
      }

      return await this.makeRequest(`/verses/${version}/${abbrev}/random`);
    } catch (error) {
      throw error;
    }
  }

  // Pesquisar versculos por palavra
  async searchVerses(version: string, searchTerm: string): Promise<any> {
    try {
      await this.ensureInitialized();

      if (!this.user) {
        throw new Error('Usurio no est autenticado');
      }

      const response = await fetch(`${API_BASE_URL}/verses/search`, {
        method: 'POST',
        headers: {
          Authorization: `Bearer ${this.user.token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          version,
          search: searchTerm,
        }),
      });

      if (!response.ok) {
        throw new Error(
          `Erro na pesquisa: ${response.status} - ${response.statusText}`
        );
      }

      return response.json();
    } catch (error) {
      throw error;
    }
  }

  // Obter verses disponveis
  async getVersions(): Promise<BibleVersion[]> {
    try {
      return await this.makeRequest('/versions');
    } catch (error) {
      throw error;
    }
  }

  // Limpar dados do usurio (logout)
  clearUser(): void {
    this.user = null;
    this.initializePromise = null;
    localStorage.removeItem('biblia_api_user');
  }

  // Obter usurio atual
  getCurrentUser(): User | null {
    return this.user;
  }
}

// Exportar instncia singleton
export const bibliaApiService = new BibliaApiService();
export type { BibleBook, BibleVerse, BibleChapter, BibleVersion };
