export interface ChatMessage {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: Date;
}

export interface ChatSession {
  id: string;
  title: string;
  messages: ChatMessage[];
  createdAt: Date;
  updatedAt: Date;
}

class ChatbotService {
  private sessions: ChatSession[] = [];
  private currentSessionId: string | null = null;
  private useApiFallback: boolean = false;
  private lastApiCall: number = 0;
  private retryCount: number = 0;
  private maxRetries: number = 3;
  private baseDelay: number = 1000; // 1 second

  constructor() {
    this.loadSessions();
  }

  // Carregar sesses do localStorage
  private loadSessions(): void {
    try {
      const saved = localStorage.getItem('bible_chat_sessions');
      if (saved) {
        const parsed = JSON.parse(saved);
        this.sessions = parsed.map((session: ChatSession) => ({
          ...session,
          createdAt: new Date(session.createdAt),
          updatedAt: new Date(session.updatedAt),
          messages: session.messages.map((msg: ChatMessage) => ({
            ...msg,
            timestamp: new Date(msg.timestamp),
          })),
        }));
      }
    } catch (error) {
      this.sessions = [];
    }
  }

  // Salvar sesses no localStorage
  private saveSessions(): void {
    try {
      localStorage.setItem(
        'bible_chat_sessions',
        JSON.stringify(this.sessions)
      );
    } catch (error) {}
  }

  // Criar nova sesso
  createSession(title?: string): ChatSession {
    const session: ChatSession = {
      id: this.generateId(),
      title: title || 'Nova Conversa',
      messages: [],
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    this.sessions.unshift(session);
    this.currentSessionId = session.id;
    this.saveSessions();

    return session;
  }

  // Obter sesso atual
  getCurrentSession(): ChatSession | null {
    if (!this.currentSessionId) return null;
    return this.sessions.find((s) => s.id === this.currentSessionId) || null;
  }

  // Definir sesso atual
  setCurrentSession(sessionId: string): ChatSession | null {
    const session = this.sessions.find((s) => s.id === sessionId);
    if (session) {
      this.currentSessionId = sessionId;
      return session;
    }
    return null;
  }

  // Obter todas as sesses
  getAllSessions(): ChatSession[] {
    return [...this.sessions].sort(
      (a, b) => b.updatedAt.getTime() - a.updatedAt.getTime()
    );
  }

  // Deletar sesso
  deleteSession(sessionId: string): boolean {
    const index = this.sessions.findIndex((s) => s.id === sessionId);
    if (index !== -1) {
      this.sessions.splice(index, 1);
      if (this.currentSessionId === sessionId) {
        this.currentSessionId = null;
      }
      this.saveSessions();
      return true;
    }
    return false;
  }

  // Renomear sesso
  renameSession(sessionId: string, newTitle: string): boolean {
    const session = this.sessions.find((s) => s.id === sessionId);
    if (session) {
      session.title = newTitle;
      session.updatedAt = new Date();
      this.saveSessions();
      return true;
    }
    return false;
  }

  // Adicionar mensagem  sesso atual
  addMessage(content: string, role: 'user' | 'assistant'): ChatMessage | null {
    const session = this.getCurrentSession();
    if (!session) return null;

    const message: ChatMessage = {
      id: this.generateId(),
      role,
      content,
      timestamp: new Date(),
    };

    session.messages.push(message);
    session.updatedAt = new Date();

    // Atualizar ttulo da sesso baseado na primeira mensagem do usurio
    if (role === 'user' && session.messages.length === 1) {
      session.title =
        content.length > 50 ? content.substring(0, 50) + '...' : content;
    }

    this.saveSessions();
    return message;
  }

  // Enviar mensagem para a IA
  async sendMessage(message: string): Promise<ChatMessage | null> {
    if (!message.trim()) return null;

    // Garantir que h uma sesso atual
    let session = this.getCurrentSession();
    if (!session) {
      session = this.createSession();
    }

    // Adicionar mensagem do usurio
    const userMessage = this.addMessage(message, 'user');
    if (!userMessage) return null;

    try {
      let aiResponse: string;

      // Tentar usar a API real primeiro, se falhar usar fallback
      if (!this.useApiFallback) {
        try {
          // Implementar rate limiting
          await this.waitForRateLimit();
          aiResponse = await this.generateAIResponse(message, session.messages);
          this.retryCount = 0; // Reset retry count on success
        } catch (error: unknown) {
          const errorMessage =
            error instanceof Error ? error.message : String(error);

          // Verificar se  erro de quota (429)
          if (errorMessage.includes('429') || errorMessage.includes('quota')) {
            this.useApiFallback = true;
            aiResponse = this.generateFallbackResponse(message);

            // Adicionar mensagem informativa sobre o status
            const quotaMessage = this.addMessage(
              ' A cota da API foi excedida. Estou funcionando com respostas baseadas em contedo bblico pr-programado. Voc pode tentar novamente mais tarde.',
              'assistant'
            );
          } else {
            // Para outros erros, tentar retry com backoff exponencial
            if (this.retryCount < this.maxRetries) {
              this.retryCount++;
              const delay = this.baseDelay * Math.pow(2, this.retryCount - 1);

              await new Promise((resolve) => setTimeout(resolve, delay));
              aiResponse = await this.generateAIResponse(
                message,
                session.messages
              );
              this.retryCount = 0; // Reset on success
            } else {
              this.useApiFallback = true;
              aiResponse = this.generateFallbackResponse(message);
            }
          }
        }
      } else {
        aiResponse = this.generateFallbackResponse(message);
      }

      // Adicionar resposta da IA
      const assistantMessage = this.addMessage(aiResponse, 'assistant');
      return assistantMessage;
    } catch (error) {
      // Adicionar mensagem de erro
      const errorMessage = this.addMessage(
        'Peo desculpas, mas ocorreu um erro ao processar sua pergunta. Por favor, tente novamente.',
        'assistant'
      );
      return errorMessage;
    }
  }

  // Implementar rate limiting para evitar 429
  private async waitForRateLimit(): Promise<void> {
    const now = Date.now();
    const timeSinceLastCall = now - this.lastApiCall;
    const minInterval = 1000; // Mnimo 1 segundo entre chamadas

    if (timeSinceLastCall < minInterval) {
      const waitTime = minInterval - timeSinceLastCall;
      await new Promise((resolve) => setTimeout(resolve, waitTime));
    }

    this.lastApiCall = Date.now();
  }

  // Gerar resposta da IA usando nova API de Responses do OpenAI
  private async generateAIResponse(
    message: string,
    conversationHistory: ChatMessage[]
  ): Promise<string> {
    const apiKey = import.meta.env.VITE_OPENAI_API_KEY;

    // Contexto do pastor evanglico
    const systemPrompt = `Voc  um pastor evanglico experiente com 80 anos de ministrio e profundo conhecimento bblico. 
    Responda sempre com base nas Escrituras Sagradas, oferecendo orientao espiritual, conforto e sabedoria bblica.
    Seja compassivo, sbio e sempre direcione as respostas para os ensinamentos de Jesus Cristo e da Bblia.
    No use emojis em suas respostas. Mantenha um tom respeitoso e pastoral.
    Cite versculos bblicos quando apropriado e oferea conselhos prticos baseados na f crist.
    Suas respostas devem ser edificantes, consoladoras e sempre apontando para Cristo.`;

    // Preparar input para a nova API de Responses
    const inputMessages = [
      // Adicionar histrico da conversa (ltimas 10 mensagens para no exceder limite)
      ...conversationHistory.slice(-10).map((msg) => ({
        role: msg.role === 'user' ? 'user' : 'assistant',
        content: [
          {
            type: 'input_text',
            text: msg.content,
          },
        ],
      })),
      {
        role: 'user',
        content: [
          {
            type: 'input_text',
            text: message,
          },
        ],
      },
    ];

    const response = await fetch('https://api.openai.com/v1/responses', {
      method: 'POST',
      headers: {
        Authorization: `Bearer ${apiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: 'gpt-4o',
        instructions: systemPrompt,
        input: inputMessages,
        max_output_tokens: 1000,
        temperature: 0.7,
        text: {
          format: {
            type: 'text',
          },
        },
      }),
    });

    if (!response.ok) {
      const errorData = await response.json();
      const errorMessage = errorData.error?.message || 'Erro desconhecido';

      // Tratamento especfico para diferentes cdigos de erro
      if (response.status === 429) {
        throw new Error(`Quota excedida: ${errorMessage}`);
      } else if (response.status === 401) {
        throw new Error(`Erro de autenticao: ${errorMessage}`);
      } else if (response.status === 403) {
        throw new Error(`Acesso negado: ${errorMessage}`);
      } else if (response.status >= 500) {
        throw new Error(`Erro do servidor: ${errorMessage}`);
      } else {
        throw new Error(`Erro da API: ${response.status} - ${errorMessage}`);
      }
    }

    const data = await response.json();

    // Extrair resposta da nova estrutura da API
    if (
      !data.output ||
      !data.output[0] ||
      !data.output[0].content ||
      !data.output[0].content[0] ||
      !data.output[0].content[0].text
    ) {
      throw new Error('Resposta invlida da API');
    }

    return data.output[0].content[0].text.trim();
  }

  // Sistema de fallback com respostas simuladas baseadas em contedo bblico
  private generateFallbackResponse(message: string): string {
    const lowerMessage = message.toLowerCase();

    // Respostas baseadas em palavras-chave bblicas
    const responses = {
      // Temas de f e relacionamento com Deus
      aproximar: `Meu querido irmo, aproximar-se de Deus  o desejo mais nobre do corao humano. Como nos ensina Tiago 4:8: "Chegai-vos a Deus, e ele se chegar a vs."

Para se aproximar do Senhor, recomendo:
- Dedique tempo dirio  orao e meditao na Palavra
- Busque um corao contrito e humilde diante do Altssimo
- Pratique a obedincia aos mandamentos divinos
- Cultive a comunho com outros irmos na f

Lembre-se das palavras de Jesus em Joo 14:6: "Eu sou o caminho, e a verdade, e a vida. Ningum vem ao Pai seno por mim."`,

      perdo: `O perdo  um dos pilares fundamentais da f crist, meu caro irmo. Como nos ensina Efsios 4:32: "Antes sede uns para com os outros benignos, misericordiosos, perdoando-vos uns aos outros, como tambm Deus vos perdoou em Cristo."

O Senhor Jesus nos ensinou no Pai Nosso: "Perdoa-nos as nossas dvidas, assim como ns perdoamos aos nossos devedores" (Mateus 6:12).

O perdo no  apenas um mandamento, mas uma libertao para nossa prpria alma. Quando perdoamos, somos libertos do peso do ressentimento e da amargura.

Colossenses 3:13 nos exorta: "Suportando-vos uns aos outros e perdoando-vos uns aos outros, se algum tiver queixa contra outro; assim como Cristo vos perdoou, assim fazei vs tambm."`,

      ansiedade: `Meu querido filho, a ansiedade  um fardo que muitos carregam nestes tempos difceis. Mas o Senhor nos oferece paz que excede todo entendimento.

Como nos ensina Filipenses 4:6-7: "No estejais inquietos por coisa alguma; antes, as vossas peties sejam em tudo conhecidas diante de Deus, pela orao e splicas, com ao de graas. E a paz de Deus, que excede todo o entendimento, guardar os vossos coraes e os vossos sentimentos em Cristo Jesus."

Jesus mesmo nos disse em Mateus 6:26: "Olhai para as aves do cu, que no semeiam, nem segam, nem ajuntam em celeiros; e vosso Pai celestial as alimenta. No tendes vs muito mais valor do que elas?"

Entregue suas preocupaes ao Senhor, pois Ele cuida de voc com amor infinito.`,

      orao: `A orao  o respirar da alma crist, meu irmo.  nossa comunicao direta com o Pai celestial.

Como nos ensina 1 Tessalonicenses 5:17: "Orai sem cessar." E Jesus nos deu o exemplo perfeito de vida de orao.

Lucas 11:9-10 nos promete: "E eu vos digo: Pedi, e dar-se-vos-; buscai, e encontrareis; batei, e abrir-se-vos-. Porque qualquer que pede recebe; e quem busca encontra; e a quem bate abrir-se-lhe-."

A orao nos conecta com Deus, fortalece nossa f, traz paz ao corao e nos alinha com a vontade divina.  atravs da orao que encontramos direo, consolo e poder para enfrentar os desafios da vida.`,

      crescer: `O crescimento espiritual  uma jornada contnua, meu caro irmo. Como nos ensina 2 Pedro 3:18: "Antes, crescei na graa e conhecimento de nosso Senhor e Salvador Jesus Cristo."

Para crescer espiritualmente:
- Alimente-se diariamente da Palavra de Deus
- Mantenha uma vida de orao constante
- Busque a comunho com outros cristos
- Pratique o amor e o servio ao prximo
- Permita que o Esprito Santo trabalhe em seu corao

Filipenses 1:6 nos assegura: "Tendo por certo isto mesmo: que aquele que em vs comeou a boa obra a aperfeioar at ao Dia de Jesus Cristo."`,

      cristo: `Ser um verdadeiro cristo vai alm de simplesmente professar a f, meu irmo.  viver como Cristo viveu.

1 Joo 2:6 nos ensina: "Aquele que diz que est nele tambm deve andar como ele andou."

Um verdadeiro cristo:
- Ama a Deus sobre todas as coisas
- Ama ao prximo como a si mesmo
- Busca viver em santidade
- Pratica a justia e a misericrdia
- Testemunha do amor de Cristo atravs de suas aes

Glatas 2:20 resume bem: "J estou crucificado com Cristo; e vivo, no mais eu, mas Cristo vive em mim; e a vida que agora vivo na carne vivo-a na f do Filho de Deus, o qual me amou e se entregou a si mesmo por mim."`,

      default: `Paz seja contigo, meu caro irmo. Agradeo por compartilhar sua pergunta comigo.

Embora cada situao seja nica, sempre encontramos orientao na Palavra de Deus. Como nos ensina Salmos 119:105: "Lmpada para os meus ps  tua palavra, e luz para o meu caminho."

Encorajo voc a buscar ao Senhor em orao, meditar nas Escrituras e buscar a orientao do Esprito Santo. Ele  fiel para nos guiar em toda verdade.

Que Deus abenoe sua jornada e lhe conceda sabedoria e paz. Estou aqui para ajud-lo no que for necessrio.`,
    };

    // Verificar palavras-chave na mensagem
    for (const [keyword, response] of Object.entries(responses)) {
      if (keyword !== 'default' && lowerMessage.includes(keyword)) {
        return response;
      }
    }

    // Verificar temas relacionados
    if (
      lowerMessage.includes('deus') ||
      lowerMessage.includes('senhor') ||
      lowerMessage.includes('jesus')
    ) {
      return responses.aproximar;
    }

    if (
      lowerMessage.includes('medo') ||
      lowerMessage.includes('preocup') ||
      lowerMessage.includes('stress')
    ) {
      return responses.ansiedade;
    }

    if (
      lowerMessage.includes('bblia') ||
      lowerMessage.includes('palavra') ||
      lowerMessage.includes('escritura')
    ) {
      return `A Palavra de Deus  nosso alimento espiritual, meu irmo. Como nos ensina Hebreus 4:12: "Porque a palavra de Deus  viva, e eficaz, e mais penetrante do que qualquer espada de dois gumes."

Josu 1:8 nos exorta: "No se aparte da tua boca o livro desta Lei; antes, medita nele de dia e de noite, para que tenhas cuidado de fazer conforme tudo quanto nele est escrito; porque, ento, fars prosperar o teu caminho e, ento, prudentemente te conduzirs."

Dedique tempo dirio para ler e meditar nas Escrituras. Elas so a fonte de sabedoria, correo e edificao para nossa vida crist.`;
    }

    return responses.default;
  }

  // Gerar ID nico
  private generateId(): string {
    return Date.now().toString(36) + Math.random().toString(36).substr(2);
  }

  // Limpar todas as sesses
  clearAllSessions(): void {
    this.sessions = [];
    this.currentSessionId = null;
    localStorage.removeItem('bible_chat_sessions');
  }

  // Mtodo para resetar o fallback (til para tentar a API novamente)
  resetApiFallback(): void {
    this.useApiFallback = false;
    this.retryCount = 0;
  }

  // Mtodo para verificar se est usando fallback
  isUsingFallback(): boolean {
    return this.useApiFallback;
  }

  // Mtodo para obter informaes sobre o status da API
  getApiStatus(): {
    usingFallback: boolean;
    retryCount: number;
    lastApiCall: number;
  } {
    return {
      usingFallback: this.useApiFallback,
      retryCount: this.retryCount,
      lastApiCall: this.lastApiCall,
    };
  }
}

export const chatbotService = new ChatbotService();
