// Hook para detectar dispositivo mvel
const useIsMobile = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth <= 768);
    };

    // Verificar imediatamente
    checkIsMobile();

    // Adicionar listener para mudanas de tamanho
    window.addEventListener('resize', checkIsMobile);

    return () => window.removeEventListener('resize', checkIsMobile);
  }, []);

  return isMobile;
};

interface BiblicalEvent {
  id: string;
  title: string;
  description: string;
  images: string[];
  videoUrl: string;
  videos?: { title: string; url: string }[];
  biblicalReference: string;
  period: string;
  location: string;
  significance: string;
  details: string;
}

