        details:
          'O Tabernculo foi montado em direo ao leste, a posio em que o sol nasce. Havia somente uma entrada para o tabernculo chamada "Porto da Corte".\n\nAs cores dos fios das cortinas dos portes eram simblicos. O azul representava a divindade, significando que a corte era um lugar de Deus. A cor prpura, uma tintura difcil e cara de se produzir, era um smbolo da realeza. O vermelho simbolizava o sangue, a cor do sacrifcio. O branco significava a santidade. A cerca do ptio, feita de linho branco, envolvia o solo sagrado, e os sacerdotes vestiam roupas brancas de linho.\n\nDiretamente da entrada de fora do ptio voc poderia ver o altar de bronze (tambm chamado de altar das ofertas queimadas). Os israelitas faziam sacrifcios dirios a Deus neste altar para expiao dos seus pecados (xodo 29:38).\n\nAssim que os primeiros sacerdotes comeavam seus servios no tabernculo, o fogo da presena do Senhor consumia o sacrifcio (Levtico 9:24). Este fogo do altar deveria ser mantido aceso o tempo todo. Os chifres do altar eram cobertos com sangue na consagrao dos sacerdotes.\n\nEntre o altar e a entrada do Santo Lugar havia um lavatrio chamado "pia". Este era o lugar onde os sacerdotes se lavavam cerimonialmente para serem consagrados ou separados para o ministrio.\n\nUma cortina cobria a entrada do Santo Lugar. Somente os sacerdotes poderiam entrar no Lugar Santo onde havia trs itens.  direita estava a Mesa dos Pes da Proposio e  frente estava o Altar do Incenso.  esquerda estava o Candelabro de Ouro.\n\nO Candelabro de Ouro tinha um formato de uma rvore com a base e o eixo central representando o tronco e com trs "galhos" de cada lado. A lmpada deveria ser cuidada por Aro e seus filhos para que a luz nunca se apagasse.\n\nNo comeo de cada semana os sacerdotes colocavam 12 pes frescos na Mesa dos Pes da Proposio  um para cada uma das 12 tribos. Isto servia como um memorial de oferta de alimento ao Senhor.\n\nAro era instrudo a queimar incenso no altar a cada manh e ao meio-dia, todos os dias, como uma oferta constante ao Senhor. Nas Escrituras, o incenso era comumente associado  orao.\n\nSomente o Sumo Sacerdote poderia atravessar a cortina do Santo dos Santos. Isto era permitido somente uma vez ao ano no Dia da Expiao. Dentro estava a Arca da Aliana.\n\nEsta tampa da Arca da Aliana com os dois querubins colocados em cima era conhecida como o propiciatrio. Deus  descrito como estando entronizado entre os querubins sobre a arca (1 Samuel 4:4, Salmo 18:10). Uma vez por ano, o Sumo Sacerdote aspergia o sangue de um touro e um bode no propiciatrio para fazer expiao pelo seu prprio pecado e pelo pecado do povo.\n\nEmbora o tabernculo fosse pesado e tivesse muitas partes, era surpreendentemente porttil. Os sacerdotes carregavam a Arca e os altares nos ombros, mas o resto era transportado em carros puxados por bois.',
